<template>
  <div class="history">
    <div class="pageHeader">
      <p class="name">ЗП AI DE Luxe картковий</p>
      <p class="iban">UA 840 3521 0600 0004 3458 9423</p>
      <div class="separator"></div>
      <p class="balance">{{balance}} <span>грн</span></p>
    </div>
    <div class="historyWrapper">
      <div class="titleBlock">Історія операцій</div>
      <div class="history">
          <div class="item" v-for="(item, i) in History" :key="i" @click="setItem(item)">
            <router-link to="detail">
              <div class="icon">
                <svg style="width:24px;height:24px" viewBox="0 0 24 24">
                  <path fill="green" d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
                </svg>
              </div>
              <p class="title">{{titleType(item.type)}}</p>
              <p class="subTitle">{{subTitleType(item.type)}}</p>
              <div class="flexBlock">
                <div class="date">{{item.date}}</div>
                <div class="sum">{{toLocaleString(item.sum)}} <span>грн</span></div>
              </div>
            </router-link>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'History',
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters(['Balance', 'History']),
    balance () {
      return this.Balance.toLocaleString()
    }
  },
  methods: {
    toLocaleString (sum) {
      return sum.toLocaleString()
    },
    titleType (type) {
      let title
      switch (type) {
        case 'p2p':
          title = 'Переказ між картками'
          break
        case 'bank':
          title = 'Банкомат'
          break
        case 'shop':
          title = 'Покупка в магазині'
          break
        case 'toUSD':
          title = 'Переказ між власними рахунками'
          break
        case 'salary':
          title = 'Заробітня плата'
          break
        default:
          title = 'Переказ між картками'
      }
      return title
    },
    subTitleType (type) {
      let subTitle
      switch (type) {
        case 'p2p':
          subTitle = 'Переказ на картку іншого банку'
          break
        case 'bank':
          subTitle = 'Отримання готівки в банкоматі іншого українського банку'
          break
        case 'shop':
          subTitle = 'Оплата товарів / послуг'
          break
        case 'toUSD':
          subTitle = 'Купівля іноземної валюти'
          break
        case 'salary':
          subTitle = 'Зарахування коштів'
          break
        default:
          subTitle = 'Переказ на картку іншого банку'
      }
      return subTitle
    },
    setItem (item) {
      const tmp = {
        titleType: this.titleType(item.type),
        subTitleType: this.subTitleType(item.type),
        ...item
      }
      this.$store.dispatch('setDetailItem', tmp)
    }
  }
}
</script>

